<template>
	<view class="container">
		<picker class="pickDate" mode="date" :value="date" :start="startData" :end="endDate" @change="bindDateChange">
			<view class="flexBet">
				<input type="text" disabled="true" placeholder="请选择下次跟进" v-model="date" placeholder-class="phcolor" />
				<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
			</view>
		</picker>
	</view>
</template>

<script>
	export default {
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				date: currentDate,
				startData: currentDate
			}
		},
		mounted() {
			this.$emit('getDate',this.date)
		},
		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			}
		},
		methods: {
			bindDateChange: function(e) {
				this.date = e.target.value
				this.$emit('getDate',this.date)
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			}
		}
	}
</script>

<style lang="scss">
	.container{
		width: 100%;
		.pickDate{
			width: 100%;
		}
	}
</style>
